const indentCode = (codeStr, indentLevel=2) => {

    const OPENERS = ['{', '(', '[']; 
    const CLOSERS = ['}', ')', ']'];
    let curIndent = 0;
    let newLines = [];
    const lines = codeStr.split(/\r?\n/);

    lines.forEach(line => {
        const lineTrimmed = line.trim();
        const lastChar = lineTrimmed[lineTrimmed.length - 1];
        const secLastChar = lineTrimmed[lineTrimmed.length - 2];
        const endOpenerFound = OPENERS.includes(lastChar);
        const endCloserFound = CLOSERS.includes(lastChar) || CLOSERS.includes(secLastChar);

        if (endCloserFound && !endOpenerFound) {
           var someOpenerFound = [...lineTrimmed].some(c => OPENERS.includes(c));
            if (!someOpenerFound) {
                curIndent -= indentLevel;
            }
        }
        newLines.push(' '.repeat(curIndent) + lineTrimmed);
        if (endOpenerFound) curIndent += indentLevel;
    });

    return newLines.join('\n');
}
