
import {colorMaker} from './../components/color_maker.js';
import * as BuilderX from '@builderx-core/builderx';

export function customPainterFlutter(propArray){
    var temp=[];
    for(let i =0; i<propArray.length; i++){
        temp.push(drawpath(propArray[i]));
    }
    return temp.join("\n")
}
export function drawpath(prop){
    var temp;

    if(isEmpty(prop)){
      temp = ``;
    }else{
     var points= BuilderX.generateSVGPath(prop.props.points,prop.props.strokeWidth, prop.props.isClosed);
     console.log(takeoutPointsForPainter(points.toString()));
  temp = `class BorderPainter${prop.styleName.toString().charAt(0).toUpperCase() + prop.styleName.slice(1)} extends CustomPainter {
  @override
  void paint(Canvas canvas, Size size) {
    Paint paint = Paint()
      ..style = PaintingStyle.stroke
      ..strokeWidth = ${prop.props.strokeWidth}
      ..color = ${colorMaker(prop.props.stroke,"")};
    Path path = Path();
    ${takeoutPointsForPainter(points.toString())}
    path.close();
    canvas.drawPath(path, paint);
  }

  @override
  bool shouldRepaint(CustomPainter oldDelegate) => false;
}

class CustomClip${prop.styleName.toString().charAt(0).toUpperCase() + prop.styleName.slice(1)} extends CustomClipper<Path> {
  @override
  Path getClip(Size size) {
    Path path = Path();
    ${takeoutPointsForPainter(points.toString())}
    path.close();
    return path;
  }

  @override
  bool shouldReclip(CustomClipper<Path> oldClipper) => false;
}
`
    }
    return temp;
}

function isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return false;
    }
    return true;
}
function takeoutPointsForPainter(code){
        // var code = "M58.72 56.35 C221.32 241.23 210.72 90.01 210.72 90.01 L122.78 200.75 C122.78 200.75 -103.88 -128.54 58.72 56.35 Z";

        var newCode = code.substring(0, code.length - 1).split(/(?=[LMC])/);
        var obj ={
        "moveTo": [],
        "lineTo":[],
        "cubicTo": []
        };
        var pathCodeArray =[];
   

        for(let i =0; i<newCode.length; i++){
      
        var temp =newCode[i].substring(1).split(/[ ,]+/).filter(function(v){return v!==''}).join(', ');
        if(newCode[i].charAt(0)=== "M"){
            var str = `path.moveTo(${temp})`;
            pathCodeArray.push(str) 
        }
            if(newCode[i].charAt(0)=== "L"){
            var str = `path.lineTo(${temp})`;
            pathCodeArray.push(str) 
        }
            if(newCode[i].charAt(0)=== "C"){
            var str = `path.cubicTo(${temp})`;
            pathCodeArray.push(str) 
        }

        }
        var str = pathCodeArray.join(";\n").replace(/^\s*[\r\n]/gm,"");
        console.log(`${str};`)
        return `${str};`;

}