import {colorMaker} from './../components/color_maker.js';
import {layoutProperties} from './../layout_properties.js';
export function iconsFlutterComponent(reactObject) {
    var flutterResultIcon = `
    Container(
        ${layoutProperties(reactObject)}
        child: Icon(
        ${reactObject.props.type}.${replaceAll(reactObject.props.name.toString(),"-", "_")},
        ${colorMaker(reactObject.props.style.color, "color:")},
        ${propCheck(reactObject.props.style.fontSize, "size")}
        ),
    ),`;
      return flutterResultIcon.replace(/^\s*[\r\n]/gm,"");
}
function propCheck(prop, key){
    var temp=``;
    if(prop === undefined){
      temp = ``
    } else{
      temp = `${key}: ${prop},`;
    }
    return temp;
}

function replaceAll(string, search, replace) {
  return string.split(search).join(replace);
}