
import {addVariable} from './../convert/add_variables.js';

export function detectState(state, res,x) {
    console.log(x["pathProps"]);
    var code;
    if(state === "statefull"){
        code = `class ConvertedWidget extends StatefulWidget {
  @override
  _ConvertedWidgetState createState() => _ConvertedWidgetState();
}

class _ConvertedWidgetState extends State<ConvertedWidget> {
${addVariable(res,x)}
  @override
  Widget build(BuildContext context) {
    return SingleChildScrollView(
        child: ${res},
        );
  }
}`;    
    }else{
        code = `class ConvertedWidget extends StatelessWidget {
${addVariable(res,x)}
@override
Widget build(BuildContext context) {
    return ${res};
}
}
`;
    }
    return code;
}


