import {propertiesDetect} from '../properties_detect.js';
export function imageFlutter(reactObject) {
       var flutterResultImage
      if(reactObject.props.source["uri"].includes('https://')){
           flutterResultImage = `Image.network(
              "${reactObject.props.source["uri"]}",
              fit: BoxFit.${reactObject.props.resizeMode === undefined? "contain":reactObject.props.resizeMode},
              ${propertiesDetect(reactObject.props.style.width, "width")}
              ${propertiesDetect(reactObject.props.style.height, "height")}
            ),`;   
      } else{
           flutterResultImage = `Image.asset(
              "${reactObject.props.source["uri"]}",
              ${propertiesDetect(reactObject.props.style.width, "width")}
              ${propertiesDetect(reactObject.props.style.height, "height")}
            ),`;
      }
      return flutterResultImage.replace(/^\s*[\r\n]/gm,"");
}