
import {propertiesDetect} from '../properties_detect.js';
import {marginFlutter} from './margin_flutter.js';
import {paddingFlutter} from './padding_flutter.js';
import {borderFlutter} from './border_flutter.js';
import {layoutProperties} from './../layout_properties.js';
export function containerFlutter(reactObject) {
    var flutterResultContainer = `Container(
  ${layoutProperties(reactObject)}
  ${propertiesDetect(reactObject.children, "child",reactObject.props.style.flexDirection, [reactObject.props.style.justifyContent,reactObject.props.style.alignItems, reactObject.props.style.alignSelf] )}

),`;
var temp = alignSelfFlutter(reactObject.props.style.alignSelf,flutterResultContainer);
      return temp.replace(/^\s*[\r\n]/gm,"");
}

function alignSelfFlutter(prop,child){
  var temp;
  if(prop === undefined){
      temp =child;
  } else{
    if(prop === "center"){
      temp = `Align(
        alignment: Alignment(0.0, -1.0),
        child: ${child}
        ),`;
    } else if(prop === "flex-end"){
       temp = `Align(
        alignment: Alignment(1.0, 1.0),
        child: ${child}
        ),`;
    }
    else if(prop === "flex-start"){
       temp = `Align(
        alignment: Alignment.topCenter,
        child: ${child}
        ),`;
    }else{
      temp =child;
    }
  }
  return temp;
}
