
import {typeDetect} from './../type_detect.js';
import {layoutProperties} from './../layout_properties.js';
import {expandChildrens} from './expand_childrens.js';
import {alignmentProperties} from './row_column_component.js';
export function listView(reactObject) {
    var flutterResult;
    var temp = `SingleChildScrollView(
        ${scrollDirection(reactObject.props.horizontal, reactObject,[reactObject.props.style.justifyContent,reactObject.props.style.alignItems, reactObject.props.style.alignSelf])}
        ),`;
    flutterResult = `Container(
        ${layoutProperties(reactObject)}
        child: ${temp}
    ),`;
    return flutterResult.replace(/^\s*[\r\n]/gm,""); 
}

function scrollDirection(prop,reactObject,alignProperties){
    var temp;
        if(prop){
            temp = `scrollDirection: Axis.horizontal,
                    child: Row(
                        ${alignmentProperties(alignProperties)}
                        children: <Widget>[ 
                           ${expandChildrens(reactObject.children)} 
                        ],
                    ),`;
        } else{
          temp = `scrollDirection: Axis.horizontal,
                    child: Column(
                        ${alignmentProperties(alignProperties)}
                        children: <Widget>[ 
                           ${expandChildrens(reactObject.children)} 
                        ],
                    ),`;
        }
    return temp;
}