import {propertiesDetect} from '../properties_detect.js';
import {paddingFlutter} from './padding_flutter.js';
import {layoutProperties} from './../layout_properties.js';
export function textFlutter(reactObject) {
    var temp;
    var flutterResultText = `Text(
            "${reactObject.children[0].value}",
            style: ${textStyle(reactObject)}
            ${textAlignProp(reactObject.props.style.textAlign)} 
            ),`;
         
        temp = `Container(
          ${layoutProperties(reactObject)}
                child: ${flutterResultText}
                ),`;
                  
      return temp.replace(/^\s*[\r\n]/gm,"");
}
export function textStyle(reactObject){

    var temp = `TextStyle(
                ${fontFamily(reactObject.props.style.fontFamily)},
                ${propertiesDetect(reactObject.props.style.fontSize, "fontSize")}
                ${propertiesDetect(reactObject.props.style.color, "color")}
                ${propertiesDetect(reactObject.props.style.letterSpacing, "letterSpacing")}
                ${propertiesDetect(reactObject.props.style.lineHeight, "height")}
                ${fontWeightProp(reactObject.props.style.fontWeight)}
                ${fontStyleProp(reactObject.props.style.fontStyle)}
                ${textDecorationProp(reactObject.props.style.textDecorationLine)}
            ),`;
            return temp;
}
function fontFamily(prop){
    var temp;
    if(prop === undefined){
        temp =``;
    }else{
        temp = `fontFamily: '${prop.replace(/ /g,'')}'`;
    }
    return temp;
}
