function fontWeightProp(prop){
     var temp;
     if(prop=== undefined){
         temp= ``;
     }else{
          if(prop === "regular"){
             temp = ``;
         }else{
         temp = `fontWeight: FontWeight.${isNaN(prop) ? prop : 'w'+prop},`;
         }
     }
     return temp;
}
function fontStyleProp(prop){
     var temp;
     if(prop=== undefined){
         temp= ``;
     }else{
        
         temp = `fontStyle: FontStyle.${prop},`;
         }
     
     return temp;
}
function textDecorationProp(prop){
     var temp;
     var propTemp;
     if(prop=== undefined){
         temp= ``;
     }else{
         if(prop==="line-through"){
             propTemp = 'lineThrough'
         }else{
             propTemp =prop;
         }
         temp = `decoration: TextDecoration.${propTemp},`;
     }
     return temp;
}
export function textAlignProp(prop){
     var temp;
     var propTemp;
     if(prop=== undefined){
         temp= ``;
     }else{
         if(prop === "auto"){
             propTemp = `start`
         }else{
             propTemp =prop;
         }
         temp = `textAlign: TextAlign.${propTemp},`;
     }
     return temp;
}
function isMargin(reactObject){
        if(reactObject.props.style.margin === undefined &&
           reactObject.props.style.marginBottom === undefined &&
           reactObject.props.style.marginHorizontal === undefined &&
           reactObject.props.style.marginLeft === undefined &&
           reactObject.props.style.marginRight === undefined &&
           reactObject.props.style.marginTop === undefined &&
           reactObject.props.style.marginVertical === undefined 
        ){
            return false;
        } 
        else{
            return true;    
        }
}

