import {propertiesDetect} from './properties_detect.js';
import {containerFlutter} from './components/container_flutter.js';
import {textFlutter} from './components/text_flutter.js';
import {imageFlutter} from './components/image_flutter.js';
import {buttonFlutter} from './components/button_flutter.js';
import {listView} from './components/listview_flutter.js';
import {textInputFlutter} from './components/text_input_flutter.js';
import {defaultWidgetFlutter} from './components/default_widget.js';
import {activityIndicatorFlutter} from './components/activity_indicator.js';
import {iconsFlutterComponent} from './../convert/third_party_components/icons_flutter.js';
import {mapViewFlutterComponent} from './../convert/third_party_components/map_view.js';
import {sliderFlutterComponent} from './components/slider_flutter.js';
import {switchFlutterComponent} from './components/switch_flutter.js';
import {ellipseFlutter} from './components/ellipse_flutter.js';
import {pathFlutter} from './components/path_flutter.js';
export function typeDetect(reactObject){
  switch (reactObject.type) {
    case "view":
    return containerFlutter(reactObject);
      break;
    case "root":
    return containerFlutter(reactObject);
      break;
    case "text":
    return textFlutter(reactObject);
      break;
    case "image":
    return imageFlutter(reactObject);
      break;
    case "touchableOpacity":
    return buttonFlutter(reactObject);
      break;
    case "scrollView":
    return listView(reactObject);
      break;
    case "textInput":
    return textInputFlutter(reactObject);
      break;
    case "activityIndicator":
    return activityIndicatorFlutter(reactObject);
      break;
    case "icon":
    return iconsFlutterComponent(reactObject);
      break;
    case "mapView":
    return mapViewFlutterComponent(reactObject);
      break;
    case "slider":
    return sliderFlutterComponent(reactObject);
      break;
    case "switch":
    return switchFlutterComponent(reactObject);
      break;
    case "ellipse":
    return ellipseFlutter(reactObject);
      break;
    case "path":
    return pathFlutter(reactObject);
      break;
    default:
    return containerFlutter(reactObject);
      break;
  }
} 



