
import {detectState} from './../convert/detect_state.js';
export function stateWidgets(res,x){
  var temp;
  if(res.search("Slider") !== -1){
    temp = detectState("statefull", res,x);
  } else if(res.search("Switch") !== -1){
    temp = detectState("statefull", res,x);
  }
  else{
     temp = detectState("stateless", res,x); 
  }
  return temp;
}