import {colorMaker} from './../components/color_maker.js';
import {layoutProperties} from './../layout_properties.js';

export function switchFlutterComponent(reactObject) {
    var flutterResultSwitch = `
Container(
${layoutProperties(reactObject)}
child: Switch(
${propCheck(colorMaker(reactObject.props.thumbTintColor,""), "inactiveThumbColor")}  
${propCheck(colorMaker(reactObject.props.thumbTintColor,""), "activeColor")}
${propCheck(colorMaker(reactObject.props.tintColor,""), "inactiveTrackColor")}  
${propCheck(colorMaker(reactObject.props.onTintColor,""), "activeTrackColor")}
value: isSwitched,
${disableSlider(reactObject.props.disabled)}
),
),`;
return flutterResultSwitch.replace(/^\s*[\r\n]/gm,"");
}

function propCheck(prop, key){
    var temp=``;
    if(prop === undefined || prop === ``){
      temp = ``
    } else{
      temp = `${key}: ${prop},`;
    }
    return temp;
}

function disableSlider(prop){
    var temp =``;
    if(prop === undefined){
        temp = `onChanged: (value) {
                    setState(() {
                        isSwitched = value;
                    });
                },`;
    }else{
        if(prop){
        temp = `onChanged: null,`
        }else{
        temp = `onChanged: (value) {
                    setState(() {
                        isSwitched = value;
                    });
                },`; 
        }
    }
    return temp;
}
