import {colorMaker} from './color_maker.js';
import {textStyle,textAlignProp} from './text_flutter.js';
import {marginFlutter} from './margin_flutter.js';

export function textInputFlutter(reactObject) {
    var flutterTextInputResult = `TextField(
${keyboardType(reactObject.props.keyboardType)}
${keyboardAppearance(reactObject.props.keyboardAppearance)}
${propcheck(reactObject.props.secureTextEntry, "obscureText")}
${propcheck(reactObject.props.autoCorrect, "autocorrect")}
${propcheck(reactObject.props.autoFocus, "autofocus")}
${propcheck(reactObject.props.maxLength, "maxLength")}
${propcheck(reactObject.props.numberOfLines, "maxLines")}
${textCaptilize(reactObject.props.autoCapitalize)}
style: ${textStyle(reactObject)}
${textAlignProp(reactObject.props.style.textAlign)}
decoration: InputDecoration(
  ${checkAvailablityOfPropString(reactObject.props.placeholder, "hintText")}
  ${hintTextStyle(reactObject.props.placeholderTextColor)}
	filled: true,
    ${fillColorFunc(colorMaker(reactObject.props.style.backgroundColor, ""), "fillColor")}
  ${inputBorder({
    "borderWidth": reactObject.props.style.borderWidth,
    "borderColor": reactObject.props.style.borderColor,
    "borderRadius": reactObject.props.style.borderRadius
    }, "enabled")}
  ${inputBorder({
    "borderWidth": reactObject.props.style.borderWidth,
    "borderColor": reactObject.props.style.borderColor,
    "borderRadius": reactObject.props.style.borderRadius
    }, reactObject.props.autoFocus)}
),      
)`;
    var result = `Container(
        ${propcheck(reactObject.props.style.width, "width")}
        ${propcheck(reactObject.props.style.height, "height")}
        ${marginFlutter({
               "margin": reactObject.props.style.margin,
               "marginLeft": reactObject.props.style.marginLeft,
               "marginRight": reactObject.props.style.marginRight,
               "marginTop": reactObject.props.style.marginTop,
               "marginBottom": reactObject.props.style.marginBottom,
               "marginHorizontal": reactObject.props.style.marginHorizontal,
               "marginVertical": reactObject.props.style.marginVertical
        })}
        child: ${flutterTextInputResult},
    ),`;
    return result.replace(/^\s*[\r\n]/gm,"");
}
function propcheck(prop, key){
    if(prop === undefined || prop === ``){
        return ``;
    }else{
        return `${key}: ${prop},`;
    }
}
function fillColorFunc(prop, key){
    if(prop === ``){
        return ``;
    }else{
        return `${key}: ${prop},`;
    }
}
function textCaptilize(prop){
    if(prop === undefined){
        return ``;
    }else{
        return `textCapitalization: TextCapitalization.${prop},`;
    }
}

function hintTextStyle(prop){
    if(prop === undefined){
        return ``;
    }else{
        return `hintStyle: TextStyle(
                ${colorMaker(prop, "color:")},
            ),`;
    }
}
function inputBorder(prop, isEnable){
    var borderRadiusLine;
    var borderWidthLine;
    var temp;
    if(prop["borderRadius"]=== undefined){
      borderRadiusLine =``;
    }else{
      borderRadiusLine=  `borderRadius: BorderRadius.all(Radius.circular(${prop["borderRadius"]})),`;
    }
    if(prop["borderWidth"]=== undefined){
      borderWidthLine =``;
    }else{
      borderWidthLine=  `borderSide: BorderSide(
          ${colorMaker(prop["borderColor"], "color:")}, 
          width: ${prop["borderWidth"]},
          ),`;
    }
    if(prop["borderRadius"] === undefined && prop["borderWidth"] === undefined){
       temp =``;
    }else{
    if(isEnable === "enabled"){
       temp = `enabledBorder: OutlineInputBorder(
		${borderRadiusLine}
		${borderWidthLine}
	   ),`;
    }else {
        if(isEnable){
        temp = `focusedBorder: OutlineInputBorder(
		${borderRadiusLine}
		${borderWidthLine}
	    ),`;
        }else{
            temp =``;
        }
    }
    } 
    return temp;
} 

function checkAvailablityOfPropString(prop, key){
     if(prop === undefined){
         return ``;
     }else{
         return `${key}: "${prop}",`;
     }
}
function keyboardAppearance(prop){
   if(prop === undefined){
        return ``;
    }else{
        if(prop === "dark"){
            return `keyboardAppearance: Brightness.dark,`;
        }else{
            return `keyboardAppearance: Brightness.light,`;         
        }
    }  
}
function keyboardType(prop){
    if(prop === undefined){
        return ``;
    }else{
       switch (prop) {
           case "number-pad":
            return `keyboardType: TextInputType.number,`;
            break;
           case "decimal-pad":
            return `keyboardType: TextInputType.numberWithOptions(decimal: true),`;
            break;
           case "numeric":
            return `keyboardType: TextInputType.number,`;
            break;
           case "email-address":
            return `keyboardType: TextInputType.emailAddress,`;
            break;
           case "phone-pad":
            return `keyboardType: TextInputType.phone,`;
            break;
           default:
            break;
       } 

    }
}
