import {typeDetect} from './../type_detect.js';
import {expandChildrens} from './expand_childrens.js';
import {alignmentProperties } from './row_column_component.js';

export function stackFlutter(childrens,alignProperties) {
     var result;
     var i;
     var tempArray =[];
     var childrenOfRowColumnStack = expandChildrens(childrens);
     for(i=0;i<childrens.length; i++){
        if(childrens[i].props.style.position === 'absolute'){
            tempArray.push(childrens[i]);
            result= `Stack(
               children: <Widget>[
               ${positionChildrens(tempArray)}
               ], 
             ),`;
        }else{
        var tempData;
        tempData = `Column(
          ${alignmentProperties(alignProperties)}
              children: <Widget>[
              ${childrenOfRowColumnStack["rowColumnData"]}
          ],
        ),`;
        if(typeof childrenOfRowColumnStack["stackData"] !== 'undefined' && childrenOfRowColumnStack["stackData"].length > 0){
          result = `Stack(
            children: <Widget>[
              ${tempData}
              ${childrenOfRowColumnStack["stackData"]}
            ],
          ),`
        }else{
          result = tempData;
        }
            
        }
     }
     return result;
}
function positionChildrens(childrens){
  var temp =[];
  var i;
  for(i =0 ; i< childrens.length; i++){ 
     
    temp.push(`Positioned(
          ${checkDef(childrens[i].props.style.top, "top")}
          ${checkDef(childrens[i].props.style.left, "left")}
          ${checkDef(childrens[i].props.style.right, "right")}
          ${checkDef(childrens[i].props.style.bottom, "bottom")}
          child: ${typeDetect(childrens[i])} 
      ),
      `);
  }
  return temp.join("");
}
function checkDef(prop, key){
    var temp;
    if(prop === undefined){
      temp = ``;
    } else{
      temp = `${key}: ${prop},`;
    }
    return temp;
}