
import {layoutProperties} from './../layout_properties.js';
import {propertiesDetect} from './../properties_detect.js';
import {colorMaker} from './../components/color_maker.js';
import {marginFlutter} from './../components/margin_flutter.js';
export function pathFlutter(reactObject) {
var viewBoxArray = reactObject.props.viewBox.split(/(\s+)/).filter( e => e.trim().length > 0);
    var flutterResult = `
FittedBox(
child: Transform(
transform: new Matrix4.identity()..scale(${reactObject.props.style.width / viewBoxArray[2]}, ${reactObject.props.style.height / viewBoxArray[3]}),    
child: Container(
width: ${viewBoxArray[2]},
height: ${viewBoxArray[3]},
${marginFlutter({
"margin": reactObject.props.style.margin,
"marginLeft": reactObject.props.style.marginLeft,
"marginRight": reactObject.props.style.marginRight,
"marginTop": reactObject.props.style.marginTop,
"marginBottom": reactObject.props.style.marginBottom,
"marginHorizontal": reactObject.props.style.marginHorizontal,
"marginVertical": reactObject.props.style.marginVertical
})}
child: Stack(
  children: <Widget>[
    ClipPath(
      clipper: CustomClip${reactObject.styleName.toString().charAt(0).toUpperCase() + reactObject.styleName.slice(1)}(),
      child: Container(
        width: ${viewBoxArray[2]},
        height: ${viewBoxArray[3]},
        ${colorMaker(reactObject.props.fill,"color:")},
      ),
    ),
    CustomPaint(
      painter: BorderPainter${reactObject.styleName.toString().charAt(0).toUpperCase() + reactObject.styleName.slice(1)}(),
      child: Container(
        width: ${viewBoxArray[2]},
        height: ${viewBoxArray[3]},
      ),
    ),
  ],
),
),
),
),`;
   return flutterResult.replace(/^\s*[\r\n]/gm,"");
}

