import {colorMaker} from './../components/color_maker.js';
import {layoutProperties} from './../layout_properties.js';

export function sliderFlutterComponent(reactObject) {
    var flutterResultSlider = `
Container(
        ${layoutProperties(reactObject)}
        child: SliderTheme(
data: SliderTheme.of(context).copyWith(
  ${propCheck(colorMaker(reactObject.props.minimumTrackTintColor,""), "activeTrackColor")}  
  ${propCheck(colorMaker(reactObject.props.maximumTrackTintColor,""), "inactiveTrackColor")}
  ${propCheck(colorMaker(reactObject.props.thumbTintColor,""), "thumbColor")}
  trackHeight: 2.0,
  thumbShape: RoundSliderThumbShape(enabledThumbRadius: 8.0),
  ${overlayThumbColor(reactObject.props.thumbTintColor)}
  overlayShape: RoundSliderOverlayShape(overlayRadius: 14.0),
),
child: Slider(
  ${propCheck(reactObject.props.step, "divisions")}
  min: ${defaultValues(reactObject.props.minimumValue, 0)}
  max: ${defaultValues(reactObject.props.maximumValue, 100)}
  value: value.toDouble(),
  ${disableSlider(reactObject.props.disabled)}
),
),
),`;
return flutterResultSlider.replace(/^\s*[\r\n]/gm,"");
}
function defaultValues(prop, value){
    var temp = ``;
    if(prop === undefined){
       temp = `${value},`;
    }else{
       temp = `${prop},`;
    }
    return temp;
}
function propCheck(prop, key){
    var temp=``;
    if(prop === undefined || prop === ``){
      temp = ``
    } else{
      temp = `${key}: ${prop},`;
    }
    return temp;
}
function overlayThumbColor(prop){
    var temp = ``;
    if(prop === undefined){
       temp= `overlayColor: Colors.grey.withAlpha(32),`;
    }else{
       temp= `overlayColor: ${colorMaker(prop,"")}.withAlpha(32),`;
    }
    return temp;
}
function disableSlider(prop){
    var temp =``;
    if(prop === undefined){
        temp = `onChanged: (double newValue) {
                 setState(() {
                    value = newValue.round();
                 });
                  },`;
    }else{
        if(prop){
        temp = `onChanged: null,`
        }else{
        temp = `onChanged: (double newValue) {
                 setState(() {
                    value = newValue.round();
                 });
                  },`; 
        }
    }
    return temp;
}
