import * as BuilderX from '@builderx-core/builderx';
import { finalConversion } from '../react-native-to-flutter-plugin';

class FlutterCodeEngine implements BuilderX.ICodeEngine {
  meta: any = {
    name: 'flutter',
    icon:
      'https://pre-cloud.builderx.io/assets/plugins/component/icons/Flutter.svg',
  };
  execute: any = (fileJSON: any) => {
  
    return finalConversion(fileJSON);
  };
}
export default class FlutterCodeGen implements BuilderX.ISessionPlugin {
  name = 'FlutterCodeGen';
  session: any;
  init(session: any) {
    this.session = session;
  }
  activate() {
    const codeEngine = new FlutterCodeEngine();
    this.session.addCodeEngine(codeEngine);
  }

  deactivate() {}

  logSession() {
    this.session.executeFunction('logSession');
  }
}
